.H-container {
  width: 50%;
  margin: auto;
  margin-top: 30px;
}

.H-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.H-col {
  width: 100%;
}

.H-box {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.H-img-text {
  width: 50%;
  height: 30vh;
}

.H-img {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.H-img,
.H-img-1:hover {
  cursor: pointer;
}

.img {
  border-radius: 50%;
  background-color: #FDC5F5;
  text-align: center;
  padding: 10px;
  filter: grayscale(1);
}

img {
  max-width: 70%;
}

.H-text h5 {
  text-align: end;
  padding-right: 25px;
  font-size: 20px;
  font-weight: 700;
  filter: opacity(0.3);
}

.H-line {
  width: 50%;
  border-top-style: dotted;
  border-right-style: dotted;
  margin-right: 28%;
  height: 22vh;
  filter: opacity(0.3);
}

.H-line-1 {
  width: 50%;
  border-top-style: dotted;
  border-left-style: dotted;
  margin-left: 27%;
  height: 22vh;
  filter: opacity(0.3);
}

.H-img-1 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.H-text-1 h5 {
  text-align: start;
  padding-left: 25px;
  font-size: 20px;
  font-weight: 700;
  filter: opacity(0.3);
}

.H-line-2 {
  width: 50%;
  border-top-style: none;
  border-left-style: none;
  margin-left: 27%;
  height: 22vh;
}

@media (max-width: 768px) {
  .H-container {
    width: 80%;
  }

  .H-img-text {
    width: 100%;
  }

  .H-text h5 {
    text-align: center;
    padding-right: 0;
  }

  .H-text-1 h5 {
    text-align: center;
    padding-left: 0;
  }

  .H-line,
  .H-line-1,
  .H-line-2 {
    width: 100%;
  }
}

/* Slider */

.S-container {
  width: 50%;
  margin: auto;
  margin-top: 10px;
}

.menu-box {
  text-align: end;
}

.icon {
  color: #000;
}

.closeIcon {
  color: red;
}

.css-6hp17o-MuiList-root-MuiMenu-list {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

.css-kk1bwy-MuiButtonBase-root-MuiMenuItem-root {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

.S-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
}

.S-slide img {
  max-width: 100%;
}

.S-pre-nex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* success */

.success {
  width: 100%;
  height: 100vh;
}